.footer {
    width: 100%;
    height: 80px;
    background-color: #000;
}

.container {
    max-width: 1040px;
    height: 100%;
    margin: auto;
    padding: 1rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer button {
    background-color:  rgb(157, 177, 229);
    color: #fff;
    font-weight: 600;
    font-size: 1rem;
    padding: 12px 18px;
    border: none;
    border-radius: 4px;
}

.footer button:hover {
    background-color: white;
    color:  rgb(110, 122, 155);
    box-shadow: rgba(87, 85, 85, 0.9) 0px 8px 14px;
    background-color: rgba(247, 247, 247, 0.9);
    transition: background-color 1s;
}
